import axios from 'axios'
import store from '@/store'
import { Message } from 'iview'
import { getToken } from '@/utils/auth'
/**
 * http
 *  token
 *
 *  
 */

// axios
const http = axios.create({
  baseURL: process.env.BASE_API, // URL
  timeout: 25000 // 
})

const loginUrls = ['/api/login', '/api/ag_login', '/api/kf_login', '/api/yy_login']

// 
http.interceptors.request.use(config => {
  if (getToken()) {
    config.headers['Authorization'] = getToken() // token-- ['X-Token']key 
  }
  return config
}, err => {
  console.log(err)
  Promise.reject(err)
})

// 
http.interceptors.response.use(response => {
  /**
     * responseerrCode
     */
  const status = response.status
  if (status === 200 || status === 201 || status === 204) {
    let res = response.data
    if ((res.data && res.data.arrTitles && res.data.arrTitles.arrString &&
         res.data.arrTitles.arrString.length &&
         res.data.arrTitles.arrString[0].startsWith('')) ||
         (res.data && res.data.szresult && res.data.szresult.startsWith(''))) {
      Message.error({
        content: ',',
        duration: 5
      })
      setTimeout(() => {
        store.dispatch('logOut').then(() => {
          location.reload()
        })
      }, 1000)
    }
    return response.data
  } else {
    return Promise.reject(new Error('error'))
  }
}, err => {
  console.log(JSON.stringify(err))
  let msg = ''
  if (err.code && err.code === 'ECONNABORTED') {
    msg = '!'
    Message.error({
      content: msg,
      duration: 5
    })
  }
  if (err.config && loginUrls.includes(err.config.url)) {
    return Promise.reject(err)
  } else {
    const response = err.response

    if (response && response.status) {
      msg = (response.data && response.data.message) ? response.data.message : ''
      if (response.status === 400) {
        msg = msg || 'Bad Request!'
      } else if (response.status === 401) {
        msg = msg || 'Unauthorized!'
      } else if (response.status === 403) {
        msg = msg || 'Forbidden!'
      } else if (response.status === 404) {
        msg = msg || 'Not Found !'
      } else if (response.status === 405) {
        msg = msg || 'Method Not Allowed!'
      } else if (response.status === 500) {
        msg = ',!'
      } else {
        msg = ',!'
      }
      Message.error({
        content: msg,
        duration: 5
      })
    }
    return Promise.reject(err)
  }
})

export default http
